.profile {
  outline-style: dashed;
  font-family: 'Helvetica Neue', sans-serif;
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px 30px;
}

.avatar {
  width: 150px;
  margin-bottom: 20px;

  /* border-radius: 50%; */
}

.name {
  margin-bottom: 10px;

  font-size: 24px;
  font-weight: 600;
}

.tag {
  margin-bottom: 10px;

  font-size: 20px;
  font-weight: 500;
  color: #737373;
}

.location {
  font-size: 20px;
  font-weight: 500;
  color: #737373;
}

.stats {
  display: flex;
  justify-content: space-evenly;

  background-color: #dfdfdf;
  /* align-items: center; */
}

.stats li {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;

  padding: 10px;
}

.stats li:not(:last-child) {
  border-right-style: groove;
}

.label {
  margin-bottom: 5px;
  font-size: 20px;
  font-weight: 500;
  color: #737373;
}

.quantity {
  font-size: 20px;
  font-weight: 500;
}
